@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;500;600;700;800&display=swap");
* {
  margin: 0;
  padding: 0;
  border: 0;
  line-height: 1.6em;
  vertical-align: baseline;
  text-decoration: none;
  outline: 0;
  box-sizing: border-box;
  background-color: transparent;
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  color: #3A3A3A;
}

html {
  scroll-behavior: smooth;
}

.container {
  max-width: 1920px;
  height: 100%;
  margin: 0 auto;
}
.container.margin-top {
  margin-top: 140px;
}

header {
  width: 100%;
  height: 140px;
  position: fixed;
  top: 0;
  background-color: #ffffff;
}
header .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
  border-left: 1px solid #B8B8B8;
  border-right: 1px solid #B8B8B8;
  border-bottom: 1px solid #B8B8B8;
}
header .container .logo {
  margin-left: 170px;
  display: flex;
}
header .container .nav_phone-wrapper {
  display: flex;
  height: 100%;
  align-items: center;
}
header .container .nav_phone-wrapper nav ul {
  display: flex;
  margin: 0 20px;
}
header .container .nav_phone-wrapper nav ul li {
  list-style-type: none;
}
header .container .nav_phone-wrapper nav ul li a {
  font-size: 25px;
  font-weight: 700;
  color: #124948;
  padding: 10px 25px;
  margin: 5px;
  display: inline-block;
  letter-spacing: 0.05em;
}
header .container .nav_phone-wrapper .phone {
  display: flex;
  align-items: center;
  padding: 0 40px;
  background-color: rgba(255, 210, 158, 0.09);
  height: 100%;
  border-left: 1px solid #B8B8B8;
}
header .container .nav_phone-wrapper .phone a {
  font-weight: 700;
  font-size: 25px;
  color: #124948;
  letter-spacing: 0.05em;
}

.hero {
  width: 100%;
  height: 740px;
  display: flex;
}
.hero .text {
  width: 680px;
  flex-shrink: 0;
  background-color: #B7D333;
  display: flex;
  align-items: center;
}
.hero .text .text-wrapper {
  margin-left: 180px;
  margin-right: 100px;
  display: flex;
  flex-direction: column;
}
.hero .text .text-wrapper h1 {
  color: #ffffff;
  font-weight: 700;
  font-size: 55px;
  text-transform: uppercase;
  line-height: 40px;
  display: inline-block;
}
.hero .text .text-wrapper h1 span {
  color: #ffffff;
  line-height: 40px;
  border-bottom: 1px solid #ffffff;
  padding-bottom: 10px;
  letter-spacing: 0.05em;
}
.hero .text .text-wrapper h2 {
  color: #ffffff;
  margin-top: 32px;
  font-weight: 600;
  font-size: 30px;
}
.hero .text .text-wrapper button {
  color: #ffffff;
  padding: 15px 30px;
  font-weight: 700;
  font-size: 20px;
  text-transform: uppercase;
  background-color: #CCE06E;
  border: 1px solid #ffffff;
  margin-top: 20px;
  cursor: pointer;
}
.hero .text .text-wrapper button:hover {
  background-color: rgba(255, 172, 75, 0.32);
}
.hero .picture {
  width: 100%;
  flex-shrink: 1;
}
.hero .picture img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.products {
  width: 100%;
  scroll-margin-top: 140px;
}
.products .product {
  display: flex;
  justify-content: space-between;
}
.products .product.no-frame {
  padding: 60px 180px;
  background-color: rgba(255, 210, 158, 0.09);
  height: 570px;
}
.products .product.frame {
  margin: 95px 180px;
  height: 500px;
}
.products .product.frame .text {
  border-top: 1px solid #8C8C8C;
  border-bottom: 1px solid #8C8C8C;
  border-left: 1px solid #8C8C8C;
  padding-left: 100px;
}
.products .product .text {
  height: 100%;
  width: 850px;
  overflow: hidden;
  padding-right: 150px;
  line-clamp: 4;
  box-orient: vertical;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.products .product .text h2 {
  font-size: 25px;
  font-weight: 700;
  line-height: 34px;
  margin-top: 10px;
}
.products .product .text h1 {
  font-size: 40px;
  font-weight: 700;
  text-transform: uppercase;
  color: #B7D333;
  letter-spacing: 0.05em;
  border-bottom: 1px solid #8C8C8C;
  line-height: 60px;
  width: fit-content;
}
.products .product .text p {
  font-weight: 400;
  font-size: 20px;
  color: #000000;
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 11;
  line-height: 30px;
  margin-top: 10px;
}
.products .product .picture {
  height: 100%;
  width: 720px;
}
.products .product .picture img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.products .product.left-pic .text {
  order: 1;
  padding-left: 130px;
  border-top: 1px solid #8C8C8C;
  border-bottom: 1px solid #8C8C8C;
  border-left: none;
  border-right: 1px solid #8C8C8C;
  padding-right: 80px;
}
.products .product.left-pic .picture {
  order: 0;
  width: 710px;
}
.products .equipments {
  height: 540px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 60px;
}
.products .equipments h2 {
  font-size: 25px;
  font-weight: 700;
  line-height: 34px;
}
.products .equipments h1 {
  font-size: 40px;
  font-weight: 700;
  text-transform: uppercase;
  color: #B7D333;
  letter-spacing: 0.05em;
  border-bottom: 1px solid #8C8C8C;
  line-height: 48px;
  width: fit-content;
  margin-bottom: 80px;
  padding-bottom: 10px;
}
.products .equipments .equipments_elem {
  display: flex;
  gap: 60px;
}
.products .equipments .equipments_elem .equipment {
  display: flex;
  flex-direction: column;
  display: inline-block;
  min-width: 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.products .equipments .equipments_elem .equipment h3 {
  font-size: 40px;
  font-weight: 700;
  color: #B7D333;
  letter-spacing: 0.05em;
  line-height: 48px;
  width: fit-content;
  margin-top: 30px;
}

.about {
  padding: 70px 600px;
  scroll-margin-top: 140px;
  background-color: rgba(255, 210, 158, 0.09);
  display: flex;
  flex-direction: column;
  align-items: center;
}
.about h2 {
  font-size: 25px;
  font-weight: 700;
  line-height: 34px;
}
.about h1 {
  font-size: 40px;
  font-weight: 700;
  text-transform: uppercase;
  color: #B7D333;
  letter-spacing: 0.05em;
  border-bottom: 1px solid #8C8C8C;
  line-height: 48px;
  width: fit-content;
  margin-bottom: 10px;
  padding-bottom: 10px;
}
.about p {
  font-weight: 400;
  font-size: 20px;
  color: #000000;
  overflow: hidden;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 11;
  line-height: 30px;
  margin-top: 10px;
  text-align: center;
}

.location {
  padding: 70px 600px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.location h2 {
  font-size: 25px;
  font-weight: 700;
  line-height: 34px;
}
.location h1 {
  font-size: 40px;
  font-weight: 700;
  text-transform: uppercase;
  color: #B7D333;
  letter-spacing: 0.05em;
  border-bottom: 1px solid #8C8C8C;
  line-height: 48px;
  width: fit-content;
  margin-bottom: 60px;
  padding-bottom: 10px;
  text-align: center;
}

.contact {
  display: flex;
  scroll-margin-top: 140px;
  justify-content: center;
  margin-bottom: 100px;
}
.contact h2 {
  font-size: 25px;
  font-weight: 700;
  line-height: 34px;
}
.contact h1 {
  font-size: 40px;
  font-weight: 700;
  text-transform: uppercase;
  color: #B7D333;
  letter-spacing: 0.05em;
  border-bottom: 1px solid #8C8C8C;
  line-height: 48px;
  width: fit-content;
  margin-bottom: 30px;
  padding-bottom: 10px;
}
.contact .form {
  border-right: 1px solid #8C8C8C;
  padding-right: 20px;
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.contact .form form {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.contact .form form input,
.contact .form form textarea {
  border: 1px solid #8C8C8C;
  padding-right: 18px;
}
.contact .form form input.active,
.contact .form form textarea.active {
  border-color: #B7D333;
}
.contact .form form input {
  height: 55px;
  width: 450px;
  text-align: right;
  font-weight: 600;
  font-size: 20px;
}
.contact .form form input::placeholder {
  font-weight: 600;
  font-size: 20px;
}
.contact .form form textarea {
  height: 260px;
  width: 450px;
  text-align: right;
  font-weight: 600;
  font-size: 20px;
  padding-top: 8px;
}
.contact .form form textarea::placeholder {
  font-weight: 600;
  font-size: 20px;
}
.contact .form form p.error {
  height: 20px;
  margin-bottom: 2px;
  font-size: 12px;
  color: #ff0000;
  padding-right: 9px;
  align-self: flex-start;
}
.contact .form form .sending-info {
  display: flex;
  margin-bottom: 0;
  font-size: 14px;
}
.contact .form form .sending-info.success-info {
  color: #8C8C8C;
}
.contact .form form .sending-info.error-info {
  color: #ffffff;
}
.contact .form form button {
  color: #ffffff;
  padding: 15px 30px;
  font-weight: 700;
  font-size: 20px;
  text-transform: uppercase;
  background-color: #CCE06E;
  border: 1px solid #ffffff;
  margin-top: 8px;
  cursor: pointer;
}
.contact .form form button:hover {
  background-color: #CEC63B;
}
.contact .form form button.loading {
  position: relative;
  pointer-events: none;
  opacity: 0.5;
}
.contact .form form button.loading::after {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 2px solid rgba(0, 0, 0, 0.2);
  border-right-color: rgba(0, 0, 0, 0.7);
  transform: translate(-50%, -50%) rotate(0deg);
  content: "";
  animation: rotateSingleLoading 0.6s infinite linear;
  z-index: 10;
}
@keyframes rotateSingleLoading {
  0% {
    transform: translate(-50%, -50%) rotate(0deg);
  }
  100% {
    transform: translate(-50%, -50%) rotate(360deg);
  }
}
.contact .form form p.error {
  margin: 0;
  margin-top: -2px;
  margin-left: 5px;
  font-size: 12px;
  color: #ff0000;
}
.contact .form form input.error,
.contact .form form textarea.error {
  border-color: #ff0000;
}
.contact .form .successSendingMessage {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  top: 50%;
  transform: translateY(-50%);
  opacity: 0;
  transition: opacity 0.5s;
  visibility: hidden;
  width: 100%;
}
.contact .form .successSendingMessage h2 {
  margin-bottom: 0;
  color: #8C8C8C;
}
.contact .form .successSendingMessage p {
  text-align: center;
}
.contact .form .explanation {
  font-weight: 400;
  font-size: 16px;
  max-width: 450px;
  margin-top: 30px;
}
.contact .address {
  padding-left: 20px;
  width: 50%;
}
.contact .address p {
  color: #000000;
  font-weight: 600;
  font-size: 25px;
}
.contact .address p.tel {
  color: #B7D333;
  text-transform: uppercase;
  font-weight: 600;
  font-size: 40px;
  max-width: 450px;
}

footer {
  width: 100%;
  height: 100px;
  background-color: #ffffff;
}
footer .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 200px;
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
  border-left: 1px solid #B8B8B8;
  border-right: 1px solid #B8B8B8;
  border-top: 1px solid #B8B8B8;
  background-color: rgba(255, 210, 158, 0.09);
  font-weight: 600;
  font-size: 20px;
}

@media (max-width: 660px) {
  .container.margin-top {
    margin-top: 90px;
  }
  header {
    height: 90px;
  }
  header .container .logo {
    margin-left: 20px;
  }
  header .container .logo img {
    height: 60px;
  }
  header .container .nav_phone-wrapper nav ul.show {
    left: 0;
    transition: left ease-out 0.5s;
  }
  header .container .nav_phone-wrapper nav ul {
    transition: left ease-out 0.5s;
    display: flex;
    position: fixed;
    top: 0;
    left: -500px;
    min-height: 100vh;
    min-width: fit-content;
    width: 30%;
    background: white;
    z-index: 3;
    flex-direction: column;
    padding: 80px 15px 0 15px;
    margin: 0;
  }
  header .container .nav_phone-wrapper nav ul li {
    padding: 10px;
  }
  header .container .nav_phone-wrapper nav ul li a {
    font-size: 20px;
  }
  header .container .nav_phone-wrapper nav .hamburger {
    padding: 10px 15px;
    border: solid 1px #313234;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 20px;
  }
  header .container .nav_phone-wrapper nav .hamburger .bars {
    position: relative;
    cursor: pointer;
  }
  header .container .nav_phone-wrapper nav .hamburger .bars .icon-bar {
    background-color: #313234;
    display: block;
    width: 15px;
    height: 2px;
    position: relative;
    transition: all 0.3s;
  }
  header .container .nav_phone-wrapper nav .hamburger .bars .icon-bar:nth-child(1) {
    transform: rotate(45deg);
    top: 5px;
  }
  header .container .nav_phone-wrapper nav .hamburger .bars .icon-bar:nth-child(2) {
    opacity: 0;
    margin: 3px 0;
  }
  header .container .nav_phone-wrapper nav .hamburger .bars .icon-bar:nth-child(3) {
    transform: rotate(-45deg);
    bottom: 5px;
  }
  header .container .nav_phone-wrapper nav .hamburger .bars.ham .icon-bar:nth-child(1) {
    transform: rotate(0);
    top: 0;
  }
  header .container .nav_phone-wrapper nav .hamburger .bars.ham .icon-bar:nth-child(2) {
    opacity: 1;
    margin: 3px 0;
  }
  header .container .nav_phone-wrapper nav .hamburger .bars.ham .icon-bar:nth-child(3) {
    transform: rotate(0);
    bottom: 0;
  }
  header .container .nav_phone-wrapper .phone {
    display: none;
  }
  .hero {
    height: 400px;
  }
  .hero .text {
    width: 280px;
  }
  .hero .text .text-wrapper {
    margin-left: 36px;
    margin-right: 0px;
  }
  .hero .text .text-wrapper h1 {
    font-size: 32px;
    line-height: 24px;
  }
  .hero .text .text-wrapper h1 span {
    line-height: 24px;
    padding-bottom: 5px;
  }
  .hero .text .text-wrapper h2 {
    font-size: 15px;
    margin-top: 20px;
    line-height: 20px;
  }
  .hero .text .text-wrapper button {
    padding: 10px 15px;
    font-size: 15px;
  }
  .hero .picture {
    width: 100%;
  }
  .products .product {
    flex-direction: column;
  }
  .products .product.no-frame {
    height: auto;
    padding: 40px 70px;
  }
  .products .product.no-frame .picture {
    display: none;
  }
  .products .product.frame {
    height: auto;
    padding: 0;
    margin: 35px 55px;
  }
  .products .product.frame .picture {
    width: 100%;
    order: 0;
  }
  .products .product.frame .text {
    order: 1;
    border-right: 1px solid #8C8C8C;
    border-bottom: 1px solid #8C8C8C;
    border-left: 1px solid #8C8C8C;
    padding: 35px 40px;
  }
  .products .product .text {
    padding: 0;
    width: 100%;
    align-items: center;
  }
  .products .product .text h2 {
    font-size: 20px;
    line-height: 27px;
    text-align: center;
  }
  .products .product .text h1 {
    font-size: 32px;
    line-height: 44px;
    width: auto;
    text-align: center;
  }
  .products .product .text p {
    text-align: center;
    font-size: 15px;
    line-height: 20px;
  }
  .products .equipments {
    height: auto;
    margin-top: 50px;
  }
  .products .equipments h2 {
    font-size: 19px;
    line-height: 26px;
  }
  .products .equipments h1 {
    font-size: 32px;
    line-height: 44px;
    margin-bottom: 40px;
  }
  .products .equipments .equipments_elem {
    flex-wrap: wrap;
    justify-content: center;
    gap: 50px;
  }
  .products .equipments .equipments_elem .equipment {
    min-width: auto;
  }
  .products .equipments .equipments_elem .equipment img {
    width: 170px;
  }
  .products .equipments .equipments_elem .equipment h3 {
    font-size: 32px;
    line-height: 44px;
    margin-top: 20px;
  }
  .about {
    padding: 50px 80px;
  }
  .about h2 {
    font-size: 19px;
    line-height: 26px;
  }
  .about h1 {
    font-size: 32px;
    line-height: 44px;
  }
  .about p {
    font-size: 15px;
    line-height: 20px;
  }
  .location {
    padding: 50px 60px;
  }
  .location h2 {
    font-size: 19px;
    line-height: 26px;
  }
  .location h1 {
    font-size: 32px;
    line-height: 44px;
    margin: 0 0 28px;
    text-align: center;
  }
  .location .map iframe {
    width: 540px;
    height: 320px;
  }
  .contact {
    flex-direction: column;
  }
  .contact .form {
    order: 1;
    width: 100%;
    align-items: center;
    padding-right: 0;
    margin-top: 50px;
  }
  .contact .form h1 {
    font-size: 32px;
    line-height: 44px;
    padding-bottom: 10px;
  }
  .contact .form form {
    align-items: center;
    margin: 0 100px;
  }
  .contact .form form input,
  .contact .form form textarea {
    text-align: left;
    padding-left: 18px;
    font-size: 20px;
    line-height: 27px;
  }
  .contact .form form input::placeholder {
    font-size: 20px;
    line-height: 27px;
  }
  .contact .form form textarea::placeholder {
    font-size: 20px;
    line-height: 27px;
  }
  .contact .form form button {
    padding: 12px 30px;
  }
  .contact .form .explanation {
    font-size: 15px;
    line-height: 25px;
    margin: 30px 100px;
    text-align: center;
  }
  .contact .address {
    order: 0;
    width: 100%;
    align-items: center;
    padding-right: 0;
    display: flex;
    flex-direction: column;
  }
  .contact .address h1 {
    font-size: 32px;
    line-height: 44px;
    padding-bottom: 10px;
    margin-bottom: 10px;
  }
  .contact .address p.tel {
    font-size: 32px;
    line-height: 44px;
  }
  footer {
    display: none;
  }
}